.title_word {
color:#ffff
   }
   .title_word_1 {
    animation-delay: 0s;
    animation: color-animation 7s linear  infinite;
  
   }
   .title_word_2 {
     animation: color-animation 7s linear infinite;
      animation-delay: 0.5s;
      
   }
   .title_word_3 {
     animation: color-animation 7s linear infinite;
      animation-delay: 1s;
     
   }
   .title_word_4 {
     animation: color-animation 7s linear infinite;
      animation-delay: 1.5s;
   
   }
   .title_word_5 {
     animation: color-animation 7s linear infinite;
      animation-delay: 2s;
   }
   .title_word_1 {
     --color-1: #ffff;
     --color-2: rgb(16, 224, 16);
   
   }
   
   .title_word_2 {
      --color-1: #ffff;
     --color-2: rgb(16, 224, 16);
   }
   
   .title_word_3 {
      --color-1: #ffff;
     --color-2: rgb(16, 224, 16);
   }
   
   .title_word_4 {
     --color-1: #ffff;
     --color-2: rgb(16, 224, 16);
   }
   .title_word_5 {
    --color-1: #ffff;
    --color-2: rgb(16, 224, 16);
  }
 
   @keyframes color-animation {
  
     0%   {color: var(--color-2)}
     50%   {color: var(--color-2)}
     51%   {color: var(--color-1)}
     100%   {color: var(--color-1)}
   }